import { useState } from "react"
import PortalGame from "./PortalGame";

const HOCGAME=(OldComp)=>{

    return function NewComponent(props){
        const[hour,setH]=useState();
        const[bill,setBill]=useState(0);
        const[form,setForm]=useState(false);
        const[pay,activePay]=useState(false);
        const[paid,setPaid]=useState(false);
        const {game}=props;
        if(form==true && game=="F5" && bill==0)
        {
            console.log("Caluclating")
            setTimeout(()=>{
                setBill(5*hour)
            },2000)
           

        }
        if(form==true && game=="King Kong" && bill==0)
            {
                console.log("Caluclating")
                setTimeout(()=>{
                    setBill(10*hour)
                },2000)
               
            }
            if(paid)
            {
                alert("Payment done success !");
                window.location.reload();
                setPaid(false)
                activePay(false)
                window.location.reload();
            }
        return (
            <div>
                <OldComp/>
                <form onSubmit={(e)=>{e.preventDefault();setForm(true)}} >
                    <label>Enter the Time you want to play</label><br/>
                    <input type="Number" max={10} onChange={(e)=>{setH(e.target.value)}}/>
                    <input type="submit" value="Submit" />
                </form>
                {form &&  <h1>Calculating..price for {game}</h1>
                }
               {bill!=0 && <h1>Your bill is : {bill}</h1>}
         
                <button type="button" onClick={()=>{activePay(true)}}>PAY</button>
                {pay && <PortalGame meth={(val)=>activePay(val)} pay={(val)=>setPaid(val)} />}
            </div>
        )

    }
}
export default  HOCGAME