import React, { useState, useEffect } from 'react';
import styles from './Investment.module.css';
import { Outlet, useNavigate } from 'react-router-dom';
import axios from 'axios'; // Add this import
import Money from './Money';
import SignUp from './SignUp';
import stocksIcon from './Icons/stock.png';
import tradingIcon from './Icons/Trading.png';
import fdIcon from './Icons/FD.png';
import overviewIcon from './Icons/overview.png';
import FD from './FD';




function Investment() {
    const navigate=useNavigate();
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [showLoginModal, setShowLoginModal] = useState(false);
  const [dateOfBirth, setDateOfBirth] = useState('');
const[loginDate,setLoginDate]=useState('');
const [signup,setSignup]=useState(false);
const[data,setUserData]=useState([])
const[overview,setOverview]=useState(true);
const[fd,setFD]=useState(false);

  // Check local storage or authentication status here
  if(isLoggedIn==false){
    const storedToken = localStorage.getItem('tel');
    if (storedToken) {
        setIsLoggedIn(true);
    }
}




  useEffect(() => {
    if(isLoggedIn==true){
      axios.get(`${process.env.REACT_APP_API_URL}?phone=${Number(window.localStorage.getItem('tel'))}`).then(resp => {
       console.log(resp.data[0].wallet);
      setUserData(resp.data[0]);
      window.localStorage.setItem('invested', resp.data[0].investment);
      window.localStorage.setItem('wallet', resp.data[0].wallet);
      window.localStorage.setItem('id', resp.data[0].id);
    }).catch(error=>{
        console.log(error);
    });
  }
    // Check local storage or authentication status here
    // For example:
    const storedToken = localStorage.getItem('authToken');
    if (storedToken) {
      setIsLoggedIn(true);
    }
  }, []);

  const handleLoginClick = () => {
    setShowLoginModal(true);
  };

  const handleCloseModal = () => {
    setShowLoginModal(false);
  };

  const handleDateOfBirthChange = (e) => {
    setDateOfBirth(e.target.value);
  };

  const handleLoginSubmit = async(e) => {
    e.preventDefault();

    // Validate the year in date of birth
    if (!isValidYear(dateOfBirth)) {
      alert('Please enter a valid date of birth with a 4-digit year.');
      return;
    }
    const data={
      phone:Number(e.target.elements.phoneNumber.value),
      dob:new Date(dateOfBirth).toISOString().split('T')[0]
    }
    //console data
    // console.log(data)
    
    try {
      // Replace 'YOUR_API_URL' with your actual API endpoint
      const response = await axios.get(`${process.env.REACT_APP_API_URL}?phone=${Number(e.target.elements.phoneNumber.value)}&dob=${new Date(dateOfBirth).toISOString().split('T')[0]}`
  );
  
      if (response.data!=null) {
        // console.log(response.data[0].investment);
        setUserData(response.data[0]);
        window.localStorage.setItem('data', response.data[0]);
        window.localStorage.setItem('invested', response.data[0].investment);
        window.localStorage.setItem('wallet', response.data[0].wallet);
        window.localStorage.setItem('id', response.data[0].id);
        // Store the phone number in localStorage
        window.localStorage.setItem('tel',e.target.elements.phoneNumber.value);
        alert("You are logged in");
        const currentTime = new Date().toLocaleString();
       setLoginDate(currentTime);
      //  setOverview(true);
       window.localStorage.setItem('overview',true);
        setIsLoggedIn(true);
        handleCloseModal();
      }
    } catch (error) {
      console.error('Login failed:', error);
      e.target.reset();
      alert('Login failed User not found. Please try again.');
      handleCloseModal();
      return;
    }
   // console.log(e.target.elements.phoneNumber.value)
   // window.localStorage.setItem('tel',e.target.elements.phoneNumber.value)
   // console.log(window.localStorage.getItem('tel'));
    // Implement your login logic here using phone number and date of birth
    // After successful login, set setIsLoggedIn(true) and close the modal
  };

  const isValidYear = (dateString) => {
    if (!dateString) return false;
    const year = new Date(dateString).getFullYear();
    return year.toString().length === 4;
  };
  let wid=window.screen.width
  var c=window.matchMedia("(orientation:portrait)").addEventListener("change",e=>{
      const potrait=e.matches;
      if(potrait)
      { 
          wid=window.screen.width
  
      }
      else
      {
          wid=window.screen.width
      }
  })
  let invest={
    "backgroundColor":"#1E40AF",
    "minHeight":"100vh",
    "maxHeight":"max-content"
   
    
  };
        if(wid<420)
        {
                invest={
                  ...invest,
                    "width":"130vw",
                   
                    "padding":"20px 0 0 0"
                 
                }
        }
        else{
            invest={
              ...invest,
           "width":"120vw",
          //  "marginLeft":"1%",
                "padding":"20px 0 0 0"

            }
        }

      const handleOverview=()=>{
       
        if(isLoggedIn)
        {
          setOverview(true);
          window.localStorage.setItem('overview',true);
        }
        else{
          
          handleLoginClick();
        }
      }

      const handleLogout=()=>{
       
        setIsLoggedIn(false);
        localStorage.clear();
        window.localStorage.setItem('loginDate',loginDate);
      }

  return (
    <div style={invest}>
        {/* here we are ging to set baackground color dark as compared to navbar */}
        {isLoggedIn &&<p style={{color:"white"}}>Last Login Time:{window.localStorage.getItem('loginDate')}</p>}
        {isLoggedIn &&<p style={{color:"white"}}>Login Time:{new Date().toLocaleString()}</p>}
    <div className={styles.investment}>
      <nav className={styles.investmentNav}>
        <a onClick={()=>navigate('/',{replace:true})}>Home</a>
        <a>Learn</a>
        {isLoggedIn ?(<a  href="">Promoter</a>) : (<>
          <a className={styles.log}  onClick={handleLoginClick}>Login</a>
          <a className={styles.log}  onClick={() =>{setSignup(true); navigate('/Investment/signup', {replace: true})}}>Sign Up</a>
</>
        )}
          {isLoggedIn && 
         //here add logout function which user click then it will remove the token from local storage and set the isLoggedIn to false 
          <p className={styles.log}  onClick={handleLogout}>Logout</p>
        }
             
      </nav>

      {showLoginModal && (
        <div className={styles.loginModal}>
          <div className={styles.loginModalContent}>
            <span className={styles.closeButton} onClick={handleCloseModal}>
              &times;
            </span>
            <h2>Login</h2>
            <form onSubmit={handleLoginSubmit}>
              <div className={styles.formGroup}>
                <label htmlFor="phoneNumber">Phone Number:</label>
                <input type="tel" id="phoneNumber" name="phoneNumber" pattern='[0-9]{10}' required />
              </div>
              <div className={styles.formGroup}>
                <label htmlFor="dateOfBirth">Date of Birth:</label>
                <input
                  type="date"
                  id="dateOfBirth"
                  name="dateOfBirth"
                  required
                  value={dateOfBirth}
                  onChange={handleDateOfBirthChange}
                />
              </div>
              <button type="submit">Login</button>
            </form>
          </div>
        </div>
      )}

      {fd && isLoggedIn && !overview &&<FD/>}

    {!showLoginModal && !fd && !isLoggedIn && !signup && <SignUp/>}

      {!showLoginModal && !fd && signup && !isLoggedIn && <Outlet/>}
      {isLoggedIn && overview && window.localStorage.getItem('overview') && <div style={{width:"80%"}}>
        <Money name={data.name}/>
        </div>}

    </div>

    <div className={styles.footerContainer}>
    <footer className={styles.footer}>
        {/* <span className={styles.footerItem}>Stocks</span>
        <span className={styles.footerItem}>Trading</span>
        <span className={styles.footerItem}>FD</span>
        <span className={styles.footerItem} onClick={handleOverview}>Overview</span> */}
      <span className={styles.footerItem}>
    <img src={stocksIcon} alt="stocks" className={styles.icon} />
    Stocks
</span>
<span className={styles.footerItem}>
    <img src={tradingIcon} alt="trading" className={styles.icon} />
    Trading
</span>
<span className={styles.footerItem} onClick={()=>{setFD(true);setOverview(false);}}>
    <img src={fdIcon} alt="fd" className={styles.icon} />
    FD
</span>
<span className={styles.footerItem} onClick={handleOverview}>
    <img src={overviewIcon} alt="overview" className={styles.icon} />
    Overview
</span>
    </footer>
    
</div>
    

    </div>
  );
}

export default Investment;
