import React from 'react';
import styles from './FD.module.css';

const FD = () => {
    const rows = [1, 2];
    
    return (
        <div className={styles.container}>
            {rows.map((rowNum) => (
                <div key={rowNum} className={styles.tableWrapper}>
                    <table className={styles.table}>
                        <caption> <p style={{backgroundColor:"black",color:"white",width:"100%",textAlign:"left"}}>--FD --</p>
                        </caption>
                        <tbody>
                            <tr>
                                {Array.from({ length: 5 }).map((_, colIndex) => (
                                    <td 
                                        key={colIndex} 
                                        className={styles.tableCell}
                                    >
                                        Cell {rowNum}-{colIndex + 1}
                                    </td>
                                ))}
                            </tr>
                        </tbody>
                    </table>
                </div>
            ))}
        </div>
    );
};

export default FD;