import { useNavigate } from "react-router-dom"
const Success=()=>{
    const navigate=useNavigate();
    return (
        <>
 <div style={{display:"flex",justifyContent:"center",backgroundColor:"white",height:"100vh",textAlign:"center"}}>
            <div style={{width:"300px",height:"300px",border:"3px solid white",borderRadius:"150px",backgroundColor:"green"}}>
            <h1 style={{color:"white",fontSize:"25px",marginTop:"50px"}}>Transaction Success!</h1><br/>
            <p style={{color:"white"}}>You will be redirected soon</p></div>
            {setTimeout(()=>navigate('/'),3000)}
        </div>
        </>
    )

}

export default Success