import { useNavigate } from "react-router-dom"

const Failed=()=>{
    const navigate=useNavigate();
    console.log("failed");
    return (
        <>
        <div style={{display:"flex",justifyContent:"center",backgroundColor:"white",height:"100vh",textAlign:"center"}}>
            <div style={{backgroundColor:"rgb(151, 18, 18)",width:"300px",height:"300px",border:"2px solid white",borderRadius:"150px"}}>
            <h1 style={{color:"white",fontSize:"25px",marginTop:"50px"}}>Transaction Failed !</h1><br/>
            <p style={{color:"white"}}>You will be redirected soon</p></div>
            {setTimeout(()=>navigate('/'),2000)}
        </div>
        </>
    )

}

export default Failed