import React,{ Component } from "react";
import PortalGame from "./PortalGame";

export class RenderPropsGame extends Component
{
    constructor(props)
    {
        super(props)
        this.state={
            pay:false,
            paid:false
        }
    }
    setPay(val)
    {
        this.setState({pay:val})
    }
    setPaid(val)
    {
        this.setState({paid:val})
        this.setState({pay:false})
    }
    

    render()
    {
        const {method}=this.props

        if(this.state.paid)
        {
            alert("Payment success !");
            this.setPaid(false);
            window.location.reload();
        }
        return (
            <React.Fragment>
                {method(20)}
                <button type="button" onClick={()=>this.setPay(true)}>PAY</button>
                {this.state.pay && <PortalGame meth={(val)=>this.setPay(val)} pay={(val)=>this.setPaid(val)}/>}
            </React.Fragment>
        )
    }
}