// export default Money;
import React,{useState} from 'react';
import styles from './Money.module.css';
import axios from 'axios';

const Money = ({name}) => {
    const [showDepositModal, setShowDepositModal] = useState(false);
    const [depositAmount, setDepositAmount] = useState('');
    const[depositAlert,setDepositAlert] = useState(false);

    const handleDeposit = () => {
        setShowDepositModal(true);
        setDepositAlert(false)
    };

    const handleContinue = async() => {
        if (!depositAmount || depositAmount < 10) {
            setDepositAlert(true);
            // alert('Please enter a valid amount');
            return;
        }
        setDepositAlert(false);
        
        const data={
            price:depositAmount,
            // quantity:quantity,
            id:window.localStorage.getItem('id'),
            name:name,
            MUID:"MUID"+Date.now(),
            transactionID:"T"+Date.now()
        }; 
        
        const response=(await axios.post("https://backendr1.onrender.com/deposit",data));
        console.log("hello--->"+response)
        window.location.href=response.data.checkoutPageUrl;
        // Handle payment logic here
        // console.log('Processing payment for:', depositAmount);
        setShowDepositModal(false);
        setDepositAmount('');
    };
    return (
        <div>
        <div className={styles.container}>
            <div className={styles.mainSection}>
                <div className={styles.card}>
                    <h2 className={styles.header}>Financial Overview</h2>
                    <div className={styles.field}>
                        <label className={styles.label}>Investment</label>
                        <span className={styles.value}>₹{window.localStorage.getItem('invested')}</span>
                    </div>
                    <div className={styles.field}>
                        <label className={styles.label}>Wallet</label>
                        <span className={styles.value}>₹{window.localStorage.getItem('wallet')}</span>
                    </div>
                </div>
                {/* <div className={styles.card}> */}
                {/* <div className={styles.buttonContainer}>
                    <button  className={`${styles.button} ${styles.deposit}`} onClick={handleDeposit} >
                        Deposit
                    </button>
                    <button className={`${styles.button} ${styles.withdraw}`}>
                        Withdraw
                    </button>
                </div> */}
                {/* </div> */}
            </div>
            <div className={styles.statsCard}>
                <h2 className={styles.header}>Performance</h2>
                <div className={styles.field}>
                    <label className={styles.label}>Total Return</label>
                    <span className={`${styles.value} ${styles.positive}`}>+₹0</span>
                </div>
                <div className={styles.field}>
                    <label className={styles.label}>Total Loss</label>
                    <span className={`${styles.value} ${styles.negative}`}>-₹0</span>
                </div>
            </div>
            {/* Card model for processing deposit */}
            {showDepositModal && (
                <div className={styles.modalOverlay}>
                    <div className={styles.modal}>
                        <h3 className={styles.modalHeader}>Deposit Amount</h3>
                        {depositAlert && <p style={{color:"red"}}>Min Amount 10</p>}
                        <div className={styles.modalContent}>
                            <input
                                type="number"
                                value={depositAmount}
                                onChange={(e) => setDepositAmount(e.target.value)}
                                placeholder="Enter amount (min  ₹10)"
                                className={styles.amountInput}
                                min={10}
                            />
                            <div className={styles.modalButtons}>
                                <button 
                                    className={styles.continueButton}
                                    onClick={handleContinue}
                                >
                                    Pay
                                </button>
                                <button 
                                    className={styles.cancelButton}
                                    onClick={() => setShowDepositModal(false)}
                                >
                                    Cancel
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </div>
        <div className={styles.buttonContainer}>
                    <button  className={`${styles.button} ${styles.deposit}`} onClick={handleDeposit} >
                        Deposit
                    </button>
                    <button className={`${styles.button} ${styles.withdraw}`}>
                        Withdraw
                    </button>
                </div>
        <div className={styles.container} style={{width:"80%",marginTop:"10%",marginLeft:"5%"}}>
    <h2 className={styles.pendingHeader}>Pending Requests</h2>
    <div className={styles.tableContainer}>
        <table className={styles.transactionTable}>
            <thead>
                <tr>
                    <th>Transaction ID</th>
                    <th>Date</th>
                    <th>Amount</th>
                    <th>Type</th>
                    <th>Status</th>
                </tr>
            </thead>
            <tbody>
                {/* Example rows - replace with actual data */}
                <tr>
                    <td>TXN123456</td>
                    <td>{new Date().toLocaleDateString()}</td>
                    <td>₹1,000</td>
                    <td>Deposit</td>
                    <td>
                        <span className={styles.pendingStatus}>Pending</span>
                    </td>
                </tr>
                <tr>
                    <td>TXN123456</td>
                    <td>{new Date().toLocaleDateString()}</td>
                    <td>₹1,000</td>
                    <td>Deposit</td>
                    <td>
                        <span className={styles.pendingStatus}>Pending</span>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</div>

        </div>
    );
};

export default Money;