import React from "react";
import styles from "./Deposit.module.css";
import { useNavigate } from 'react-router-dom';
const DepositFailed = () => {
    const navigate = useNavigate();
    return (
        <div className={styles.container}>
            <div className={styles.card}>
                <h1 className={styles.title}>Deposit Failed</h1>
                <p className={styles.message}>
                    Unfortunately, your deposit could not be processed. Please try again later or contact support.
                </p>
                <button className={styles.retryButton} onClick={() => navigate('/Investment')}>
                    Retry
                </button>
            </div>
        </div>
    );
};

export default DepositFailed;