import React,{ Component, PureComponent } from "react";
import Context from "./Context";
import F5Game from "./F5Game";
import King_Kong from "./King_Kong";
import { RenderPropsGame } from "./RenderPropsGame";
import { Mario } from "./Mario";

export class ListFeatures extends Component
{
    constructor(props)
    {
        super(props)
        this.state={
            count:0,
            game:"0",
            King_Kong:["STRONG","HEALTHY","HUNGRY","DANGEROUS"],
            F5:["FAST","SPEEDY","DIFFICULT"],
            show:false
        }
        
    }
   game=this.props;
   componentDidUpdate()
   {
   console.log("-->",this.game);
   }
    setGame(game)
    {
        console.log("Set gme");
        console.log(game)
            this.setState({game:game});

    }

    increment()
    {
        this.setState((prev)=>({count:prev+1}))
    }

    display_feature()
    {
       
            
               if(this.state.game.toString()==="King Kong"){ 
              return  this.state.King_Kong.map((value,index)=>{
                    console.log("MAP IS WORKING");
                   return <li key={index}>{value}</li>
                })   
            }   
            else if(this.state.game.toString()==="F5"){
                {
                    return  this.state.F5.map((value,index)=>{
                        console.log("MAP IS WORKING");
                       return <li key={index}>{value}</li>
                    }) 
                }
            }
                else if(this.state.game.toString()==="Mario")
                {
                    return <h1>Not Available</h1>
                }

            
        
    }
    setShow()
    {
        this.setState({show:true})
    }


    render()
    {
      return ( <Context.Consumer>
       { (game)=>{
        if(this.state.game!=game){
                    this.setGame(game);
        }
return (
    <React.Fragment>
         <h1>
             Thank You for Your Input
         </h1>
      
             <ol>
                 {this.display_feature()} 
             </ol>
        
         <button style={{fontSize:"30px"}} onClick={()=>this.setShow()}>Click me to know the Price of Play</button>
         {this.state.show && this.state.game=="F5" && <F5Game game="F5"/>}
         {this.state.show && this.state.game=="King Kong" && <King_Kong game="King Kong"/>}
         {this.state.show && this.state.game=="Mario" && <RenderPropsGame method={(val)=><Mario game="Mario" charge={val} /> }/>}

    </React.Fragment>
)
       }
        }

       </Context.Consumer>
      )
       
       

        
    }
}