import { Component } from "react";
import ReactDOM from "react-dom"
 const PortalGame=(props)=>
    {
        const {meth,pay}=props
        console.log(meth);
    return ReactDOM.createPortal(
    <div style={{backgroundColor:"green",width:"90vh",fontSize:"50px",height:"50vh",color:"white",position:"absolute",top:"20vh",left:"55vh",opacity:"90%",justifyItems:"center"}}>
        <h1>PAY</h1>
        <button type="button" onClick={()=>{pay(true)}}>CLICK TO PAY</button>
        <button style={{fontSize:"30px"}} onClick={()=>{meth(false)}}>close</button>
        </div>
        ,document.getElementById("portal"))
    }
export default PortalGame