import { Component } from "react";

export class Mario extends Component{
    constructor(props) {
        super(props)
    }

    render()
    {
        const {game,charge}=this.props
        return (
            <div>
                <h1>{game}</h1>
                <h1>Charge is : {charge}</h1>
            </div>
        )
    }
}