import { useEffect, useRef, useState } from "react";
import style from "./Home.module.css"
import ReactDOM from 'react-dom';
import { useNavigate } from "react-router-dom";
const Description=({descp,but})=>{
    const navigate=useNavigate();
   
 
    const myRef=useRef(1)

    console.log("hello");
    console.log();
    // const changeQuantity=()=>{
    //    setQuantity((p)=>{return p+1;});
    // }
    const placeOrder=()=>{
        console.log(myRef.current);
        navigate('/order',{state:{quantity:myRef.current,desc:descp}})
    }
    return ReactDOM.createPortal(
    <div className={style.description}>
        {/* <div><h1>Description</h1></div> */}
      <div style={{display:"flex",justifyContent:"right"}}><button style={{width:"auto",backgroundColor:"white",border:"none",marginRight:"1px",paddingRight:"5px"}} onClick={()=>{but()}}>&times;</button></div> 
        <table>
            <tbody>
                <tr>
                    <td colspan="3"><img src={descp.item} style={{width:"50%"}} /></td>
                    {/* <td>{descp.description}<br/>₹{descp.price}</td> */}
                </tr>
                <tr>
                <td colspan="3" ><b>{descp.description}</b><br/>₹{descp.price}</td>
                    
                </tr>
                <tr>
                <td><form ><label style={{fontSize:"10px",backgroundColor:"yellow"}}><i>Note -Default quantity is 1</i></label><br/>Quantity-<input type="number"  onChange={(e)=>{myRef.current=e.target.value}} style={{width:"10%"}}/></form></td>
                </tr>
                <tr>
                  
                    <td colspan="3"><button onClick={()=>{return placeOrder()}}  className={style.button} style={{fontSize:"20px",width:"200px",borderRadius:"0 0 0 0",marginBottom:"5%"}}>Buy</button></td>
                  
                </tr>
            </tbody>
        </table>
        <table style={{width:"90%",border:"2px solid black",marginLeft:"5%",marginBottom:"10%"}}>
            <caption style={{fontSize:"25px",fontWeight:"500"}}>Details</caption>
            <tbody>
            <tr>
                    <th style={{width:"30%",border:"2px solid black",height:"10px"}}>About this Item :</th>
                    <td style={{textAlign:"left",width:"50%"}}>{descp.detail}</td>
                </tr>
            </tbody>
        </table>
    </div> ,document.getElementById('portal'))
   
}
export default Description