import { Component } from "react"
import { ErrorHandling } from "./ErrorHanling";

export class ListGames extends Component
{
    constructor(props)
    {
        super(props)
        this.state={
            name:"",
            error:false
        }

    }
   
   
    render()
    {
        const {game}=this.props;

     function setGame(name1,game)
        {
            console.log("game name = ",name1);
            game(name1);
        }
     
    
        return (
          
            <div>
                <form>
                    <select name="game_Select"style={{height:"auto"}} onChange={(event)=>{setGame(event.target.value,game)}}>
                        <option>select</option>
                        <option value="King Kong">King Kong</option>
                        <option value="Mario">Mario</option>
                        <option value="F5">F5</option>
                    </select>
                </form>
            </div>
           
        )
    
    }
    componentDidMount()
    {
        console.log(" Chiled mounted");
    }
    componentDidUpdate()
    {
        console.log(" chiled updated");
    }
}