import React,{useState} from 'react';
import styles from './Deposit.module.css';
import { useLocation, useNavigate } from 'react-router-dom';
import { useEffect } from 'react';
import axios from 'axios';

const DepositSuccess = () => {
    const navigate = useNavigate();
    const location=useLocation();
    const query=new URLSearchParams(location.search)
    const data=query.get('data');
    const iv=query.get('iv');
    const key=query.get('key');
    const [redirect,setRedirect] = useState(false);
   
    // alert("running"+id+" "+amount);

                setTimeout(()=>{setRedirect(true)},5000);
if(data && iv && key){
    console.log(data,iv,key);
    if(data.includes("SHAZEV") && iv.includes("UMAIR") && key.includes("IMAAD"))
    {

        return (
            <div className={styles.container}>
                <div className={styles.card}>
                    <div className={styles.icon}>✓</div>
                    <h1 className={styles.title}>Deposit Successful!</h1>
                    <p className={styles.message}>
                        Your deposit has been processed successfully. Thank you for using our service!
                    </p>
                   {!redirect?<div>
                    <div className={styles.loader}></div>
                    <p>Please wait..</p> </div>:<button 
                        className={styles.button} 
                        onClick={() => navigate('/Investment')}
                    >
                        Go to Dashboard
                    </button>}
                </div>
            </div>
        );
    }
    else{
        return (
            <div>
             Token is missing
            </div>
         )
    }
}
    else
    {
        return (
           <div>
            Token is missing
           </div>
        )
    }
};

export default DepositSuccess;