import { Component, PureComponent } from "react";
import { ListGames } from "./ListGames";
import { ErrorHandling } from "./ErrorHanling";
import Context from "./Context";
import style from'./themeCss.module.css';
import { ListFeatures } from "./ListFeatures";
export class Game extends PureComponent{

    constructor(props)
    {
        super(props)
        this.state={
            select:false,
            game:"",
            theme:null
           
        }

    }

    select(option)
    {

       this.setState({select:option,game:""});
    }
    setGame(val)
    {
        console.log(val);
        this.setState({game:val});
    }
    setTheme(theme)
    {
        this.setState({theme:theme})
        console.log(this.state);
        
    }

    render()
    {
        
    return (
        <ErrorHandling>
            <Context.Consumer>
                {(theme)=>
                { 
                    if(this.state.theme!=theme)
                    { this.setTheme(theme)
                    }
                    return (
                      <div  style={{color:this.state.theme=="white"?"black":"white",minHeight:"auto",backgroundColor:this.state.theme}}>
                      <h1>Welcome to Game ! {this.state.theme}</h1>
                      <h2>Do You want to select your game</h2>
                      <button onClick={()=>{this.select(true)}}>Yes</button><button onClick={()=>{this.select(false)}}>no</button>
                      {this.state.select && <ListGames game={(val)=>{this.setGame(val)}}/>}
                      {this.state.game && this.state.select && <h1 style={{width:"400px",border:this.state.theme=="white"?"2px solid black":"2px solid white",fontFamily:"sans-serif",justifySelf:"center"}}>{this.state.game}</h1>}
                      {this.state.game && this.state.select && <Context.Provider value={this.state.game}><ListFeatures game={this.state.game} /></Context.Provider> }
                 </div>
                )}}
            </Context.Consumer>
     
       </ErrorHandling>
    )
    }
  
    componentDidMount()
    {
        console.log(" Parent rendered");
    }
}