import React, { useRef, useState } from 'react';
import styles from './SignUp.module.css';
import axios from 'axios';

const SignUp = () => {
    const [dateOfBirth, setDateOfBirth] = useState('');
    let otp=useRef();
    // ...existing code...

    const handleSubmit = async (event) => {
        event.preventDefault();
        const phone = event.target.elements.phone.value;
        if (phone.startsWith('0')) {
            alert('Phone number should not start with 0.');
            return;
        }
        const formData = {

            name: event.target.elements.name.value,
            phone: Number(event.target.elements.phone.value),
            dob: new Date(dateOfBirth).toISOString().split('T')[0],
            investment: 0,
            wallet: 0,
            address: String(event.target.elements.address.value),
            email: String(event.target.elements.email.value),
        };
     
        try {
            try {
                const response = await axios.get(`${process.env.REACT_APP_API_URL}?phone=${formData.phone}`);

                if (response.data && response.data.length > 0) {
                 
                    alert('User with this phone number already exists!');
                    return;
                }
            } catch (error) {
                if (error.response.data == "Not found") {

                    //--------------Generator OTP ---------------------------
                    const generateOTP = () => {
                        return Math.floor(1000 + Math.random() * 9000); // Generates a 4-digit random number
                    };

                     otp = generateOTP();
                    console.log('Generated OTP:', otp);

                    // ---------------------------------------------------------------
                                // api for sending otp will we wrote here
                    //----------------------------------------------------------------

                    axios.post(process.env.REACT_APP_API_URL, formData)
                        .then(response2 => {
                            
                          
                            if (response2.data) {
                                alert('Registration successful!');
                                // Clear form or redirect user
                                event.target.reset();
                                window.location.href = '#/Investment';
                            }
                        }).catch((error) => {

                            console.log("Post Error" + error);
                        })
                }
               
                else {
                    throw new Error(error.response.data);
                }
            }

        }

        catch (error) {
            console.error('Registration failed:', error);
            alert('Registration failed. Please try again.');
        }
    };


    return (
        <div className={styles.signUpContainer}>
            <div className={styles.signUpCard}>
                <h2 className={styles.title}>Sign Up</h2>
                <form onSubmit={handleSubmit} className={styles.form}>
                    <div className={styles.formGroup}>
                        <label htmlFor="name">Full Name:</label>
                        <input type="text" id="name" name="name" required />
                    </div>
                    <div className={styles.formGroup}>
                        <label htmlFor="phone">Phone Number:</label>
                        <input
                            type="tel"
                            id="phone"
                            name="phone"
                            pattern="[0-9]{10}"
                            placeholder="10 digit mobile number"
                            required
                        />
                    </div>
                    <div className={styles.formGroup}>
                        <label htmlFor="dateOfBirth">Date of Birth:</label>
                        <input
                            type="date"
                            id="dateOfBirth"
                            name="dateOfBirth"
                            value={dateOfBirth}
                            onChange={(e) => setDateOfBirth(e.target.value)}
                            required
                        />
                    </div>
                    <div className={styles.formGroup}>
                        <label htmlFor="address">Address:</label>
                        <textarea
                            id="address"
                            name="address"
                            rows="3"
                            required
                        />
                    </div>
                    <div className={styles.formGroup}>
                        <label htmlFor="email">Email:</label>
                        <input type="email" id="email" name="email" required />
                    </div>

                    <button type="submit" className={styles.submitButton}>Sign Up</button>
                </form>
            </div>
        </div>
    );
};

export default SignUp;